
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 28px;
}

.layout {
  background-color:#aaaaaa;
  border: 3px solid #2b2b2b;
  padding: 2px;
  border-radius: 3px;
  text-align: center;
  width: 688px;
  margin:auto;
}
.key {
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  color: white;
  border-radius: 3px;
  margin: 3px;
  display: inline-block;
  text-align: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  font-size: 11px;
  vertical-align: middle;
  position: relative;
}
.key:hover {
  background-color: #88aa00 !important;
  cursor: pointer;
}
.light {
  background-color: #494949;
}
.key.dark {
  background-color: #2b2b2b;
}
.key.view-upper span {
  position: absolute;
  top: -9px;
  left: 17px;
}
.key.view-upper span + span {
  top: 6px;
  left: 16px;
}
.key.pressed {
  background-color: #88aa00;
}

.screen textarea {
 width: 688px;
 height: 200px;
}
